import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from 'src/app/services/validation.service';

@Component({
  selector: 'app-depot-branch-create',
  templateUrl: './depot-branch-create.component.html',
  styleUrls: ['./depot-branch-create.component.css']
})
export class DepotBranchCreateComponent implements OnInit {

  depotbranchcreateform: FormGroup;
  formCtrlKeys: any[] = [];
  submitted = false;

  constructor(fb: FormBuilder, private validationService: ValidationService) {
    this.depotbranchcreateform = fb.group({
      'depotname': ['', Validators.required],
      'rocno': ['', [Validators.required, Validators.minLength(8), Validators.maxLength(12)]],
      'ownname': ['', Validators.required],
      'nric': ['', [Validators.required, Validators.minLength(8), Validators.maxLength(12)]],
      'email': ['', [Validators.required, Validators.email]],
      'addr1': ['', Validators.required],
      'addr2': [''],
      'postcode': ['', [Validators.required, Validators.minLength(5), Validators.maxLength(5)]],
      'state': ['', Validators.required],
      'country': ['MY', Validators.required],
      'cntno1': ['', [Validators.required, Validators.minLength(10), Validators.maxLength(11)]],
      'cntno2': ['', [Validators.minLength(10), Validators.maxLength(11)]],
      'nolots': ['', [Validators.required, Validators.minLength(1), Validators.maxLength(2)]],
      'nocont': ['', [Validators.required, Validators.minLength(2), Validators.maxLength(3)]],
      'isgargavil': ['']
    });
  }

  ngOnInit() {
  }

  onChangeValidation(data: any) {
    this.validationService.depotFormValidation(data);    
  }
  
  get f() { return this.depotbranchcreateform.controls; }

  onSubmit(depotbranchcreateform: any) {
    this.submitted = true;
   
    // stop here if form is invalid
    if (this.depotbranchcreateform.invalid) {
      return;
    }
    this.formCtrlKeys.length = 0;
    Object.keys(depotbranchcreateform.value).forEach(key => {
      this.formCtrlKeys.push(key);
    });
    console.log(this.formCtrlKeys);

  }
}
