import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder } from '@angular/forms';
import { ValidationService } from 'src/app/services/validation.service';
declare var $: any;
@Component({
  selector: 'app-customer-search',
  templateUrl: './customer-search.component.html',
  styleUrls: ['./customer-search.component.css']
})
export class CustomerSearchComponent implements OnInit {

  customersearchform: FormGroup;
  
  constructor(private router: Router,fb: FormBuilder, private validationService: ValidationService) {  
     
    this.customersearchform = fb.group({
    'compname': [''],
    'rocno': [''],
    'ownname': [''],
    'nric': [''],
    'email': [''],
    'cntno1': [''],
    'postcode':[''],
    'state': ['']
  });
}

ngOnInit() {
  $(document).ready(function() {
    $('#example').DataTable();
} );
}

onChangeValidation(data: any) {
  this.validationService.depotFormValidation(data);    
}


  addCustomer(){
    this.router.navigateByUrl('/customercreate');
  }

}
